  .contenedor-productos .productosContainer {
    width: 20rem;
    height: 30rem;
    box-shadow: 9px 6px 6px -2px rgba(0, 0, 0, 0.39);
    -webkit-box-shadow: 9px 6px 6px -2px rgba(0, 0, 0, 0.39);
    -moz-box-shadow: 9px 6px 6px -2px rgba(0, 0, 0, 0.39);
    text-align: center;
