*{
    margin: 0;
    padding: 0;
    box-sizing:border-box;
    text-decoration: none;
    font-family: sans-serif;
}
body{
    
     background-size: cover;
     background-repeat: no-repeat;
     background-position: center;
     background-attachment: fixed;
 }
 main{
    width: 100%;
    padding: 20px;
    margin: auto;
    margin-top: 100px;
}
.caja__trasera {
    div{
    margin: 100px 40px;
    color: white;
    transition: all 500ms;
    }
    
}
.caja__trasera div p,
.caja__trasera{
    div {
    button{
    margin-top: 30px;
} 
   
}
.caja__trasera{
    div{
        h3{
            font-weight: 400;
    font-size: 26px;
        }
    }

}  
    
}
.caja__trasera{
    button{
        padding: 10px 40px;
        border: 2px solid #fff;
        background: transparent;
        font-size: 14px;
        font-weight: 600;
        cursor: pointer;
        color: white;
        outline: none;
        transition: all 300ms;
    }
} 

/*Formularios*/
.contenedor__login-register{
    display: flex;
    align-items: center;
    width: 80%;
    max-width: 340px;
    position: relative;
    top: 360px;
    left: 380px;

    transition: left 500ms cubic-bezier(0.175, 0.885, 0.320, 1.27);
}
.contenedor__login-register{
    form{
        width: 100%;
        padding: 80px 20px;
        background: #fff;
        position: absolute;
        border-radius: 20px;
    }
} 
.contenedor__login-register{
    form{
        h2{
            font-size: 30px;
            text-align: center;
            margin-bottom: 20px;
            color: #D1562E;
            
          } 
          input{
            width: 100%;
            margin-top: 5px;
            padding: 10px;
            border: none;
            background: #F2F2F2;
            font-size: 16px;
            outline: none;
        }
        button{
            padding: 10px 40px;
            margin-top: 40px;
            border: none;
            font-size: 14px;
            background: #D1562E;
            color: white;
            cursor: pointer;
            outline: none;
        }

    }
} 
